.mainContainer {
  display: flex;
  height: 515px;
}
.leftBlock {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-family: 'Heebo', sans-serif;
}
.helloBlock {
  height: 105px;
  flex-shrink: 0;
  margin-left: 140px;
}
.textBlock {
  height: 66px;
  margin-top: 40px;
  margin-left: 140px;
}
.rightBlock {
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.photoContainer {
  width: 243px;
  height: 243px;
  border-radius: 50%; /* Делает элемент круглым */
  background: url('../../../public/photo/mih.jpg') lightgray 50% / cover no-repeat;
  box-shadow: 0px 4px 8px 0px rgba(0,0,0,0.2); /* Добавление тени */
}
.textBlock {
  perspective: 1000px; /* Добавляет перспективу для 3D-трансформаций */
}

.englishText, .russianText {
  width: 100%;
  backface-visibility: hidden; /* Скрывает обратную сторону при перевороте */
  transition: transform 0.6s linear; /* Анимация переворота */
  position: absolute;
}

.englishText {
  transform: rotateY(0deg); /* Начальное положение для английского текста */
}

.russianText {
  transform: rotateY(-180deg); /* Начальное положение для русского текста */
}

.textBlock:hover .englishText {
  transform: rotateY(180deg); /* Переворот при наведении на английский текст */
}

.textBlock:hover .russianText {
  transform: rotateY(0deg); /* Переворот при наведении на русский текст */
}
.downloadResume {
  width: 208px;
  height: 47px;
  border-radius: 2px;
  background: #FF6464;
  margin-top: 120px;
  margin-left: 140px;
  border: none;
  color: #FFF;
  cursor: pointer;

  font-family: Heebo;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}
.downloadResume:active {
  background-color: #CC5252; /* Цвет фона при нажатии */
}
