.tlt {
    margin: 20px 0 0 20px;
    color: #21243D;
    font-family: Heebo;
    font-size: 26px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
}
.postContainer p {
    margin: 17px 20px 10px 20px;
    color: #21243D;
    font-family: Heebo;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}
.txt, .translatedText {
    margin: 0 20px 0 20px;
    color: #21243D;
    font-family: Heebo;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}
.translatedText {
    margin-top: 20px;
}
.postContainer {
    perspective: 1000px;
    width: 30%; /* или другая ширина по вашему выбору */
    margin-right: 20px;
    height: 295px; /* Высота контейнера */
    display: flex;
    flex-direction: column;
    border-radius: 4px;
    background: #FFF;
    box-shadow: 0px 4px 10px 0px rgba(187, 225, 250, 0.25);
}

.postInner {
    position: relative;
    width: 100%;
    height: 100%;
    transition: transform 0.6s;
    transform-style: preserve-3d;
}

.postContainer:hover .postInner {
    transform: rotateY(180deg);
}

.postFront, .postBack {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
}

.postBack {
    transform: rotateY(180deg);
    /* Здесь можно добавить стили для обратной стороны поста */
}

