.containerCard {
    display: flex;
    height: 200px;
    margin-left: 140px;
    position: relative;
}
.containerCard:not(:first-child) {
    margin-top: 30px;
}
.containerCard::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 75%;
    height: 2px; /* Высота черты */
    background-color: #E0E0E0; /* Цвет черты, измените по желанию */
}

.photoCard img {
    width: 246px;
    height: 180px;
    border-radius: 6px;
}
.read {
    display: block;
}
.tlt {
    margin-left: 18px;
    color: #21243D;
    font-family: Heebo;
    font-size: 30px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
}
.blockOval {
    margin-top: 20px;
    margin-left: 18px;
    width: 62px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 25px;
    border-radius: 16px;
    background: #142850;

    color: #FFF;

    font-family: Heebo;
    font-size: 18px;
    font-style: normal;
    font-weight: 900;
    line-height: 60px;
}
.txt {
    margin-top: 22px;
    margin-left: 18px;
    height: 93px;
    width: 60%;
    color: #21243D;
    font-family: Heebo;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}
