.containerHead {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: 80px;
    background-color: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    width: 100%;
}
.containerHead li {
    list-style: none; /* Убирает стандартные маркеры списка */
    padding: 10px; /* Расстояние вокруг пунктов меню */
    position: relative; /* Для позиционирования линии под текстом */
    cursor: pointer; /* Изменяет курсор при наведении */
}
.containerHead li::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0;
    height: 2px;
    background-color: #494646; /* Цвет линии */
    transition: width 0.3s ease-in-out; /* Плавное увеличение ширины */
}
.containerHead li:hover::after {
    width: 100%; /* Линия растягивается на всю ширину при наведении */
}
